const Button = ({ color, text, onClick }) => {
    const buttonStyle = {
        backgroundColor: color,
        color: '#fff',
    };

    return (
        <button onClick={onClick} class='btn' style={buttonStyle}>
            {text}
        </button>
    );
};

Button.defaultProps = {
    color: 'black',
    text: 'Add',
};

export default Button;
