import { useState } from 'react';

import Header from './components/Header';
import Tasks from './components/Tasks';

function App() {
    const [tasks, setTasks] = useState([
        {
            id: 1,
            text: 'Doctors Appoinment',
            day: 'Feb 5th at 2:50pm',
            reminder: true,
        },
        {
            id: 2,
            text: 'Meeting at School',
            day: 'Feb 6th at 1:30pm',
            reminder: true,
        },
        {
            id: 3,
            text: 'Food Shopping',
            day: 'Feb 6th at 4:00pm',
            reminder: false,
        },
        {
            id: 4,
            text: 'Picking up a friend',
            day: 'Feb 6th at 3:00am',
            reminder: true,
        },
    ]);

    //Add Task
    const addTask = newTask => {
        setTasks([
            { id: Math.ceil(Math.random() * 10000), ...newTask },
            ...tasks,
        ]);
    };

    //Delete Task
    const onDelete = id => {
        setTasks(tasks.filter(task => task.id !== id));
    };

    //Toggle Remainder
    const toggleReminder = id => {
        setTasks(
            tasks.map(task =>
                task.id === id ? { ...task, reminder: !task.reminder } : task
            )
        );
    };

    return (
        <div class='card' style={{ minWidth: '400px', width: '60vw' }}>
            <Header onAdd={addTask} />
            <div class='card-body'>
                {tasks.length > 0 ? (
                    <Tasks
                        tasks={tasks}
                        onDelete={onDelete}
                        onToggle={toggleReminder}
                    />
                ) : (
                    <div class='text-secondary text-center'>No tasks</div>
                )}
            </div>
        </div>
    );
}

export default App;
