const Task = ({ task, onDelete, onToggle }) => {
    return (
        <div class='card mb-2' onDoubleClick={() => onToggle(task.id)}>
            <div class='card-body d-flex justify-content-between'>
                <div>
                    <h6 class={task.reminder ? 'text-success' : ''}>
                        {task.text}
                    </h6>
                    {task.day}
                </div>
                <button
                    class='btn btn-sm btn-danger'
                    onClick={() => onDelete(task.id)}
                >
                    Delete
                </button>
            </div>
        </div>
    );
};

export default Task;
